import React from "react";
// import "assets/mycss";
import validator from "validator";
import '../login/Login.css'
//storage =         

// redux 
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as authActions from "redux/modules/auth";
import * as userActions from "redux/modules/user";
import * as AuthApi from "lib/api/AuthApi";
//   Components
import AuthError from "components/error/AuthError";

import { Button, Grid, Image, Form, Segment, Container, Dimmer, Loader } from 'semantic-ui-react'

//debouce    ,   ,     .
import debounce from "lodash/debounce";

interface validate {
  [name: string]: (value: string) => boolean;
}

class Join extends React.Component<any, any> {
  state = { isMailSending : false}
  componentWillUnmount() {
    const { AuthActions } = this.props;
    AuthActions.initializeForm("join");
  }
  setError = (message: any, name: string) => {
    const { AuthActions } = this.props;
    AuthActions.setError({
      form: "join",
      message,
      name
    });
  };
  validate: validate = {
    email: (value: string) => {
      if (!validator.isEmail(value)) {
        this.setError("   .", "email");
        return false;
      }
      this.setError(null, "email");
      return true;
    },
    userid: (value: string) => {
      if (
        !validator.isAlphanumeric(value) ||
        !validator.isLength(value, { min: 4, max: 15 })
      ) {
        this.setError(
          " 4~15      .",
          "userid"
        );
        return false;
      }
      return true;
    },
    password: (value: string) => {
      if (!validator.isLength(value, { min: 8 })) {
        this.setError(" 8  .", "password");
        return false;
      }
      this.setError(null, "password"); //    null     
      return true;
    },
    passwordConfirm: (value: string) => {
      if (this.props.form.get("password") !== value) {
        this.setError("  .", "passwordConfirm");
        return false;
      }
      this.setError(null, "passwordConfirm");
      return true;
    }
  };

  //  

  checkEmailExists = debounce(async (email: string) => {
    const { AuthActions } = this.props;
    try {
      await AuthActions.checkEmailExists(email);
      if (this.props.exists.get("email")) {
        this.setError("  .", "email");
      } else {
        this.setError(null, "email");
      }
    } catch (e) {
      console.log(e);
    }
  }, 300);

  checkUsernameExists = debounce(async (userid: string) => {
    const { AuthActions } = this.props;
    try {

      await AuthActions.checkUsernameExists(userid);

      if (this.props.exists.get("userid")) {
        this.setError("  .", "userid");
      } else {
        this.setError(null, "userid");
      }
    } catch (e) {
      console.log(e);
    }
  }, 300);

  handleChange = (e: any) => {
    const { AuthActions } = this.props;
    const { id, value } = e.target;
    AuthActions.changeInput({
      id,
      value,
      form: "join"
    });
    //  

    const validation = this.validate[id](value);
    if (id.indexOf("password") > -1 || !validation) return; //  ,    

    // TODO: ,   
    const check =
      id === "email" ? this.checkEmailExists : this.checkUsernameExists; // name       
    check(value);
  };

  handleLocalRegister = async () => {
    const { form, AuthActions, UserActions, history } = this.props;
    const { email, userid, password, passwordConfirm } = form.toJS();
    const { validate } = this;
    // if (error === true) return; //      
    if (
      !validate["email"](email) ||
      !validate["userid"](userid) ||
      !validate["password"](password) ||
      !validate["passwordConfirm"](passwordConfirm)
    ) {
      //    
      return;
    }
    try {
      await AuthActions.localRegister({
        email,
        userid,
        password
      });
      this.setState({isMailSending : true }, async ()=>{
        await AuthApi.sendSignupEmail(email);
        UserActions.setValidated(true);
        this.setState({isMailSending : false},)
        history.push("/join/complete"); //    
      })
      // const loggedInfo = this.props.result.toJS();
      // console.log("", loggedInfo);
      // // TODO:    (/)
      // storage.set("loggedInfo", loggedInfo);
      // UserActions.setLoggedInfo(loggedInfo);
    } catch (e) {
      // TODO:   ERROR 
      if (e.response.status === 409) {
        const { key } = e.response.data;
        const message =
          key === "email"
            ? "  ."
            : "  .";
        return this.setError(message, key);
      }
    }
  };
  render() {
    const { isMailSending } = this.state;
    const { error } = this.props;
    const error2 = error.toJS();
    const { email, userid, password, passwordConfirm } = this.props.form.toJS();
    const { handleChange, handleLocalRegister } = this;
    return (
      <Container>
        <Dimmer active={isMailSending} inverted>
          <Loader content='    ...' />
        </Dimmer>
        <Grid
          textAlign="center"
          style={{ height: "70vh" }}
          verticalAlign="middle"
        >
          <Grid.Column style={{ maxWidth: 450 }}>
            {/* <Header as="h2" color="orange" textAlign="center"> */}
            <Image centered size="tiny" src="/images/logo1.png" />
            {/* </Header> */}


            <Form size="large">
              <Segment stacked>
                <AuthError error={error2.email} />
                <Form.Input
                  fluid
                  value={email}
                  onChange={handleChange}
                  icon="user"
                  iconPosition="left"
                  id="email"
                  placeholder=" "
                  type="text"
                  nametag=""
                  autoCapitalize="none"
                />
                <AuthError error={error2.userid} />
                <Form.Input
                  fluid
                  value={userid}
                  onChange={handleChange}
                  id="userid"
                  icon="user outline"
                  iconPosition="left"
                  placeholder=" "
                  type="text"
                  nametag=""
                  autoCapitalize="none"
                />
                <AuthError error={error2.password} />
                <Form.Input
                  fluid
                  value={password}
                  onChange={handleChange}
                  id="password"
                  icon="lock"
                  iconPosition="left"
                  placeholder=" "
                  type="password"
                  nametag=""
                  autoCapitalize="none"
                />
                <AuthError error={error2.passwordConfirm} />
                <Form.Input
                  fluid
                  value={passwordConfirm}
                  onChange={handleChange}
                  id="passwordConfirm"
                  icon="check square"
                  iconPosition="left"
                  placeholder="  "
                  type="password"
                  nametag=" "
                  autoCapitalize="none"
                />

                {/* <div className="policy">
            <Icon name="check circle outline" />
              <span onClick={() => this.setState({ termPopup: true })}>
                
              </span>
          </div> */}
                <Button
                  className="login"
                  inverted
                  valuex="true"
                  fluid
                  size="large"
                  onClick={handleLocalRegister}
                >
                  
                </Button>
              </Segment>
            </Form>

          </Grid.Column>
        </Grid>
      </Container>
    );
  }
}

export default connect(
  (state: any) => ({
    form: state.auth.getIn(["join", "form"]),
    error: state.auth.getIn(["join", "error"]),
    exists: state.auth.getIn(["join", "exists"]),
    result: state.auth.get("result")
  }),
  dispatch => ({
    AuthActions: bindActionCreators(authActions, dispatch),
    UserActions: bindActionCreators(userActions, dispatch)
  })
)(Join);
